/*
IMPORTANT:
In order to preserve the uniform grid appearance, all cell styles need to have padding, margin and border sizes.
No built-in (selected, editable, highlight, flashing, invalid, loading, :focus) or user-specified CSS
classes should alter those!
*/


.slick-header.panel-heading,
.slick-footer.panel-footing {
	width: 100%;
	overflow: hidden;
	border-left: 0px;
}

.slick-header-columns,
.slick-footer-columns {
	width: 999999px;
	position: relative;
	white-space: nowrap;
	cursor: default;
	overflow: hidden;
}

.slick-column-name {
	font-weight: bold;
}

.slick-header-column.panel-heading,
.slick-footer-column.panel-footing {
	position: relative;
	display: inline-block;
	overflow: hidden;
	text-overflow: ellipsis;
	height: 12pt;
	line-height: 12pt;
	margin: 0;
	padding: 6px;
	border: 1px solid transparent;
	border-bottom: 1px solid silver;
	float: left;
}

.slick-header-column:not(:last-child),
.slick-footer-column:not(:last-child) {
	border-right: 1px solid silver;
}

.slick-header-column-sorted {
	font-style: italic;
}

.slick-sort-indicator {
	display: inline-block;
	width: 8px;
	height: 5px;
	margin-left: 4px;
}

.slick-sort-indicator-desc {
	background: url(images/sort-desc.gif);
}

.slick-sort-indicator-asc {
	background: url(images/sort-asc.gif);
}

.slick-resizable-handle {
	position: absolute;
	font-size: 0.1px;
	display: block;
	cursor: col-resize;
	width: 4px;
	right: 0px;
	top: 0;
	height: 100%;
}

.slick-sortable-placeholder {
	background: silver;
}

.grid-canvas {
	position: relative;
	outline: 0;
}

.slick-viewport {
	overflow: hidden;
}

.grid-header {
	padding: 5px;
}

.slick-row.ui-widget-content, .slick-row.ui-state-active {
	position: absolute;
	border: 0px;
}

.slick-cell {
	float: left;
	border: 1px solid transparent;
	border-bottom-color: silver;

	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	vertical-align: middle;
	z-index: 1;
	padding: 3px 4px 4px 3px;
    margin: 0;
    font-Weight: normal !important;
    
	white-space: nowrap;

	cursor: default;
}

.slick-cell:not(:last-child) {
	border-right: 1px solid silver;
}

.slick-cell.highlighted {
    background: lightskyblue;
    background: rgba(0,0,255,0.2);
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    transition: all 0.5s;
}

.slick-cell.flashing {
    border: 1px solid red !important;
}

.slick-cell.editable {
	z-index: 11;
	overflow: visible;
	background: white;
	border-color: black;
	border-style: solid;
}

.slick-cell:focus {
	outline: none;
}

.slick-reorder-proxy {
	display: inline-block;
	background: blue;
	opacity: 0.15;
	filter: alpha(opacity=15);
	cursor: move;
}

.slick-reorder-guide {
	display: inline-block;
	height: 2px;
	background: blue;
	opacity: 0.7;
	filter: alpha(opacity=70);
}

.slick-selection {
    z-index: 10;
    position: absolute;
    background: gray;
    border: 1px solid black;
    opacity: 0.3;
    filter: alpha(opacity=30);
    -webkit-box-shadow: 0px 0px 10px black;
    -moz-box-shadow: 0px 0px 10px black;
    box-shadow: 0px 0px 10px black;
}
